.footer {
    position: fixed;
    display: flex;
    justify-content: space-between;
    bottom: 0;
    height: 65px;
    width: 96%;
    background-color: #282828;
    padding: 20px;
}

.footer__left {
    flex: 0.3;
    display: flex;
    align-items: center;
    color: white;
    width: 300px;

}

.footer__green {
    color: #1ed15e;
}

.footer__image {
    height: 60px;
    width: 60px;
    margin-right: 20px;
    object-fit: contain;
}


.footer__songInfo>h4 {
    margin-bottom: 5px;
}

.footer__songInfo>p {
    font-size: 12px;
}

.footer__center {
    flex: 0.4;
    padding: 0 100px;
    color: white;
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 300px;
}

.footer__right {
    flex: 0.3;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
    padding: 20px;
}

.footer_icon:hover,
.footer_green:hover {
  transition: transform 0.2s ease-in-out;
  transform: scale(1.2) !important;
}

.footer__right * .MuiSlider-root{
    color: white;
}